
import './App.css';

import Navbar from './components/Navbar';
import Newsbox from './components/Newsbox';
import LoadingBar from 'react-top-loading-bar'

import React, { Component } from 'react'

import {
  BrowserRouter as Router,
  Routes,
  Route,
} from "react-router-dom";

export class App extends Component {
  state = {
    progress: 0
  };
  setProgress = async (progVlaue)=>{
    this.setState({
      progress: progVlaue
    })
  }

  render() {
    return (
      <div>
        <Router>
          <LoadingBar
            color='#f11946'
            progress={this.state.progress}
            onLoaderFinished={this.setProgress}
          />
          <Navbar />
          <Routes>
            <Route exact path="/" element={<Newsbox setProgress={this.setProgress} key="home" pageSize={6} country="in" category="general" />} />
            <Route exact path="/business" element={<Newsbox setProgress={this.setProgress} key="business" pageSize={6} country="in" category="business" />} />
            <Route exact path="/entertainment" element={<Newsbox setProgress={this.setProgress} key="entertainment" pageSize={6} country="in" category="entertainment" />} />
            <Route exact path="/general" element={<Newsbox setProgress={this.setProgress} key="general" pageSize={6} country="in" category="general" />} />
            <Route exact path="/health" element={<Newsbox setProgress={this.setProgress} key="health" pageSize={6} country="in" category="health" />} />
            <Route exact path="/science" element={<Newsbox setProgress={this.setProgress} key="science" pageSize={6} country="in" category="science" />} />
            <Route exact path="/sports" element={<Newsbox setProgress={this.setProgress} key="sports" pageSize={6} country="in" category="sports" />} />
            <Route exact path="/technology" element={<Newsbox setProgress={this.setProgress} key="technology" pageSize={6} country="in" category="technology" />} />
          </Routes>

        </Router>
      </div>
    )
  }
}


export default App;
