import React, { Component } from 'react'

export class NewsItem extends Component {

    render() {
        let { title, description, imageUrl, url, author, date, source } = this.props;
        return (
            <div>
                <div className="card my-3">
                    <span className="position-absolute top-0 translate-middle badge rounded-pill bg-danger" style={{left:"90%", zIndex:"10"}}>
                        {source}
                    </span>
                    <img src={imageUrl ? imageUrl : "https://www.timesnownews.com/technology-science/james-webb-space-telescope-begins-hunt-for-the-first-stars-and-habitable-worlds-article-92885070"} className="card-img-top" alt="img" />
                    <div className="card-body">

                        <h5 className="card-title">{title}</h5>
                        <p className="card-text">{description}</p>
                        <p className="card-text"><small className='text-muted'>By {author} , Published on {new Date(date).toGMTString()}</small></p>
                        <a href={url} target="_blank" rel="noopener noreferrer" className="btn btn-dark">Read More</a>
                    </div>
                </div>

            </div>
        )
    }
}

export default NewsItem