import React, { Component } from 'react'
import NewsItem from './NewsItem'
import Spinner from './Spinner';
import PropTypes from 'prop-types'
import InfiniteScroll from 'react-infinite-scroll-component';

export class Newsbox extends Component {
    

    static defaultProps = {
        country: "in",
        pageSize: 6,
        category: "general"
    }
    static propTypes = {
        country: PropTypes.string,
        pageSize: PropTypes.number,
        category: PropTypes.string
    }
    articles = [];
    constructor() {
        super();
        this.state = {
            articles: this.articles,
            loading: true,
            page: 1,
            totalArticles: 0
        }
    }
    updateNews = async () => {
        this.props.setProgress(10);
        let url = `https://newsapi.org/v2/top-headlines?country=us&category=${this.props.category}&apiKey=c15d257c867b434c9c430d6e770d8e20 &page=${this.state.page} &pageSize=${this.props.pageSize}`;
        let data = await fetch(url);
        let dataFetch = await data.json();
        this.props.setProgress(60);
        this.setState({
            page: this.state.page,
            articles: dataFetch.articles,
            loading: false,
            totalArticles: dataFetch.totalResults
        })
        this.props.setProgress(100);
    }
    async componentDidMount() {
        this.updateNews();
    }

    handleNext = async () => {
        this.setState({
            loading: true,
            page: this.state.page + 1
        })
        this.updateNews();


    }
    handlePrevious = async () => {
        this.setState({
            loading: true,
            page: this.state.page - 1
        })
        this.updateNews();
    }

    fetchMoreData = async ()=>{
        this.setState({
            page: this.state.page + 1
        })
        let url = `https://newsapi.org/v2/top-headlines?country=us&category=${this.props.category}&apiKey=c15d257c867b434c9c430d6e770d8e20 &page=${this.state.page} &pageSize=${this.props.pageSize}`;
        let data = await fetch(url);
        let dataFetch = await data.json();
       
        this.setState({
            page: this.state.page,
            articles: this.state.articles.concat(dataFetch.articles),
            loading: false,
        })
           
    }

    render() {
        return (
            <>
                    <h2 className="text-bold text-center" style={{ margin: '40px' }}>IITGN News Website - Top Headlines</h2>

                    <InfiniteScroll
                        dataLength={this.state.articles.length}
                        next={this.fetchMoreData}
                        style={{ display: 'flex', flexDirection: 'column-reverse' }} //To put endMessage and loader
                        hasMore={this.state.articles.length !== this.totalResults}
                        // loader={<h4>Loading...</h4>}
                        scrollableTarget="scrollableDiv"
                    >
                        
                    <div className="container">

                    <div className="row my-3">
                        {this.state.loading && <Spinner />}
                        {this.state.articles.map((element) => {
                            return <div className="col-md-4" key={element.source.id?element.source.id : new Date(element.publishedAt).toGMTString()}>
                                <NewsItem title={element.title ? element.title : ""} description={element.description ? element.description : ""} imageUrl={element.urlToImage ? element.urlToImage : "https://static01.nyt.com/images/2011/01/14/arts/14MOVING-span/MOVING-jumbo.jpg"} url={element.url} author={element.author ? element.author : "Author Unknown"} date={element.publishedAt} source={element.source.name} />
                            </div>
                        })}

                    </div>
                    </div>
                    </InfiniteScroll>
                    {/* <div className="container buttons d-flex justify-content-between">
                        <button disabled={this.state.page <= 1} type="button" className="btn btn-dark" onClick={this.handlePrevious}>&#8592; Previous</button>
                        <button disabled={this.state.page + 1 > Math.ceil(this.state.totalArticles / this.props.pageSize)} type="button" className="btn btn-dark" onClick={this.handleNext}>Next &#8594; </button>
                    </div> */}

            </>
        )
    }
}

export default Newsbox